import { t } from 'sn-translate';

const getRevisionString = (articleResult, versioningInstalled) => {
    if(versioningInstalled && (articleResult.revisedBy || articleResult.baseVersion)){
        return t('Revised by {0}',articleResult.revisedBy);
    } else {
        return t('Authored by {0}', articleResult.author);
    }
};

export default (props) => {
    const { articleResult, versionDetails } = props.payload;
    let versioningInstalled = false;
    if(versionDetails)
        versioningInstalled = versionDetails.isInstalled;
    return (
        <div className="sn-card-footer kb-footer">
            <div className="kb-footer-left">
                <div className="sn-card-content kb-meta">
                    <div className="sn-meta-list-item">
                        <span>{getRevisionString(articleResult,versioningInstalled)}</span>
                    </div>
                    <span className="kb-meta-pipe">|</span>
                    <div className="sn-meta-list-item">
                        <span>{articleResult.number}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}
