import ArticleTemplate from "./articleTemplate";
import ArticleHeader from './articleHeader';
import ArticleFooter from './articleFooter';
import _timeToRead from "./utils/timeToReadUtil";
import {clone} from "lodash";
import { t } from 'sn-translate';

const blocksDetail = (block) => {
	let userCriteriaDetail = ``;
	if(block.can_read_user_criteria)
		userCriteriaDetail = `<span>`+t('Can Read') + ' - ' + block.can_read_user_criteria+`</span>`;
	if(block.cannot_read_user_criteria)
		userCriteriaDetail += `<span>`+t('Cannot Read') + ' - ' + block.cannot_read_user_criteria+`</span>`;
	return `<span><now-icon icon="eye-outline" size="sm" /></span><span>`+block.number+`</span><span>`+block.short_description+`</span>`+userCriteriaDetail;
}

const _selectKnowledgeBlocksForSection = (state, content, blocks) => {
	const parentDiv = document.createElement('div');
	parentDiv.innerHTML = content.data.props.innerHTML;
	blocks.forEach(block => {
        const kbBlock = parentDiv.querySelector('.' + block.number + ':not(.block-selected)');
		if (kbBlock) {
			if (!state.properties.hideBlocksInformation) {
				kbBlock.classList.add('block-selected');
				const childDiv = document.createElement('div');
				childDiv.id = 'detail_' + block.number;
				childDiv.classList.add('block-detail');
				childDiv.innerHTML = blocksDetail(block);
                kbBlock.parentNode.insertBefore(childDiv, kbBlock);
			} else {
				kbBlock.classList.remove('block-selected');
				const detailDiv = document.getElementById('detail_' + block.number);
				if (detailDiv) {
					parentDiv.removeChild(detailDiv);
				}
			}
		}
	});
	content.data.props.innerHTML = parentDiv.innerHTML;
}

const selectKnowledgeBlocks = (state, content,blocks) => {
    // If `content` is an array
	// `content` contains data for each section (corresponding to respective HTML Editor) in an article

    if (content instanceof Array) {
        for (let item of content) {
            let sectionalContent = item
                && item.children
                && item.children[0]
                && item.children[0].children
                && item.children[0].children[1];

            let collapsableContent = sectionalContent
                && sectionalContent.children
                && sectionalContent.children.length
                && sectionalContent.children[0];
            let contentToRender = collapsableContent || sectionalContent;
            if (contentToRender) {
                _selectKnowledgeBlocksForSection(state, contentToRender,blocks);
            }
        }
    } else {
        // If `content` is an object
        _selectKnowledgeBlocksForSection(state, content, blocks);
    }
};

/** 
 * @seismicView sn-knowledge-content
*/
export const view = (state, {dispatch}) => {
	let articleResult = null;
	let article_html = null;
	if (!state.articleDataObj) return null;
	const articleData = state.articleDataObj;
	if (!articleData.articleSysId) return null;
	articleResult = articleData.articleInfo;
	article_html = articleData.articleHTML;
	const versionDetails = articleData.versionDetails;
	const userDetail = {};
	if (!articleResult)
		return null;
	let contentHTML = article_html || "";
	const props = state.properties;
	const hideArticleHeader = props.hideArticleHeader;
	const hideAuthorInfo = props.hideAuthorInfo;
	let articleContentHTML = (
		articleData.templateFieldData.length ? articleData.templateFieldData.map(field => {
			contentHTML += field.content;
			return <ArticleTemplate
				label={field.label}
				content={field.content}
				column={field.column}
				type={field.type}
				collapsible={field.collapsible}
				collapsed={field.collapsed}
				headingStyle={field.headingStyle}
				fieldStyle={field.fieldStyle}
				dispatch={dispatch}
			/>
		})
			: createElement('div', { props: { innerHTML: article_html } })
	);

	let blocks = (props.blocksInformation != null && props.blocksInformation != '') ? JSON.parse(props.blocksInformation) : [];

	let content = clone(articleContentHTML);
	if (blocks && blocks.length > 0) {
		selectKnowledgeBlocks(state,content,blocks);
		articleContentHTML = content;
	}
	return (
		<div className="sn-card -no-border ">
			{hideArticleHeader? "" : <ArticleHeader payload={{ articleResult,userDetail}} props={props} artileTimeToRead={_timeToRead(contentHTML, "en")}/>}
			<div className ="kb-content">
			{articleContentHTML}
			</div>
			<slot name="attachments"/>
			{hideAuthorInfo? "" : <ArticleFooter payload={{ articleResult,versionDetails}} props={props}/>}
		</div>
	);
}
