import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './view';
import {actionHandlers} from './actions';
import styles from './knowledgeContent.scss';
import { SnTimeAgo } from 'sn-component-timeago';
import {secondary} from './sampleData';
import { nowLinkInterceptorBehavior } from '@devsnc/uxf-shared-behaviors';

const getArticleHTMLContent = props =>{
	let response = [];
	let contentHTML = "";
	if(props.articleDetails!=null){
		contentHTML = props.articleDetails.articleHTML;
		if(props.articleDetails.templateFieldData != null && props.articleDetails.templateFieldData.length > 0)
			props.articleDetails.templateFieldData.map(field => {
				contentHTML += field.content;
			});
		response.push(contentHTML)
	}
	return response;
}

const timeAgoBehaviors = SnTimeAgo.getBehaviors();

/**
 * Knowledge Content is a seismic component used to render articles.
 * It can also be used to render knowledge templates and knowledge blocks.
 *
 * ```jsx
 * <sn-knowledge-content
	    articleDetails={articleData} secondaryFields = {secondary}
	></sn-knowledge-content>
 * ```
 *
 * @seismicElement sn-knowledge-content
 * @summary A seismic component that is used to render articles as well as knowledge templates and knowledge blocks.
 * @uib.label Knowledge content
 * @uib.icon tile-uib-article-content-1-sm
 * @uib.description Seismic component for knowledge content which handles knowledge templates and knowledge blocks
 * @uib.category component
 * @uib.associatedTypes global.core, global.visualization
 * @uib.properties articleDetails, hideArticleHeader, secondaryFields, hideKbCategoryInfo, hideNumberLink, hideAuthorInfo, hideActions, hideBlocksInformation, blocksInformation
 * @uib.slots actions, attachments
 */
createCustomElement('sn-knowledge-content', {
	renderer: {type: snabbdom},
	view : view,
	actionHandlers : actionHandlers,
	properties : {
		/**
		 * Information about the article record such as short description and version
		 * @type {{articleHTML: string, articleSysId: string, templateFieldData: {column: string, content: string, fieldStyle: string, headingStyle: string, label: string, type: string, collapsed: boolean, collapsible: boolean}[], kbDocSysId: string, articleInfo: {author: string, kbCategory: string, workflowState: string, kbKnowledgeBase: string, number: string, shortDescription: string, sysId: string, sysUpdatedOn: string, sysViewCount: string, rating: string, revisedBy: string, baseVersion: string, isExpired: boolean, officeDocUrl: string}}}
		 * @uib.label Article details
		 * @uib.description Information about the article record such as short description and version
		*/
		articleDetails : {default:''},
		/**
		 * Hide the article header
		 * @type {boolean}
		 * @uib.label Hide article header
		 * @uib.description Hide the article header
		*/
		hideArticleHeader : {default:false},
		/**
		 * Hide the category breadcrumb in article header
		 * @type {boolean}
		 * @uib.label Hide category information
		 * @uib.description Hide the category breadcrumb in article header
		*/
		hideKbCategoryInfo: {default:false},
		/**
		 * Hide article actions in article header
		 * @type {boolean}
		 * @uib.label Hide article actions
		 * @uib.description Hide article actions in article header
		*/
		hideActions: {default:false},
		/**
		 * Hide article number link in header
		 * @type {boolean}
		 * @uib.label Hide number link
		 * @uib.description Hide article number link in header
		*/
		hideNumberLink : {default:true},
		/**
		 * Secondary fields to include in article header
		 * @type {{icon: string, text: string, value: string, type: ('string'|'date'|'timeToRead')}[]}
		 * @uib.label Secondary fields
		 * @uib.description Secondary fields to include in article header
		*/
		secondaryFields:{ default:[]},
		/**
		 * Hide author information in footer
		 * @type {boolean}
		 * @uib.label Hide author information
		 * @uib.description Hide author information in footer
		*/
		hideAuthorInfo : {default :false},
		/**
		 * Hide knowledge block information (only applicable when Knowledge Blocks plugin is active)
		 * @type {boolean}
		 * @uib.label Hide block information
		 * @uib.description Hide knowledge block information (only applicable when Knowledge Blocks plugin is active)
		*/
		hideBlocksInformation: {default: true},
		/**
		 * Knowledge block information (only applicable when Knowledge Blocks plugin is active)
		 * @type {string}
		 * @uib.label Block information
		 * @uib.description Knowledge block information (only applicable when Knowledge Blocks plugin is active)
		*/
		blocksInformation: {default: ''}
	},
	initialState :{
		articleDataObj : {default:''}
	},
	behaviors: [...timeAgoBehaviors,
		{
			behavior: nowLinkInterceptorBehavior,
			options: {
				propsToHtmlArray: getArticleHTMLContent
			}
		}
	],
	styles:styles
});
