import {FIELD_COLLAPSED} from './constants';
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED, COMPONENT_RENDERED } = actionTypes;

const componentRendered = ({ host }) => {
	let content = host.shadowRoot.querySelector(".kb-content");
	if(content) {
		content.querySelectorAll("a").forEach(link => {
			if(link.href.includes("#")){
				let targetID = link.href.split("#")[1];
				link.onclick = () => {
					var target = content.querySelector("#"+targetID) ? content.querySelector("#"+targetID) : content.querySelector("[name="+targetID+"]");
					if(target) {
						target.scrollIntoView({
							behavior: 'smooth',
							block: 'start'
						});
						return false;
					}
				};
			}
		});
	}
}

export const actionHandlers = {
	[FIELD_COLLAPSED]: {
		effect : ({action,state,updateState}) =>{
			let kbObj = state.articleDataObj;
			kbObj.templateFieldData.forEach(function (item) {
				if (item.column == action.payload.column) {
					item.collapsed = !item.collapsed;
				}
			});
			updateState({
				articleDataObj: {...kbObj}
			});
		}

	},

	[COMPONENT_BOOTSTRAPPED]: {
		effect: ({updateState,properties}) =>{
			let articledata = properties.articleDetails;
			updateState({
				articleDataObj: articledata,
			});
		}
	},
	[COMPONENT_PROPERTY_CHANGED]: {
		effect : ({updateState,properties})=>{
			let articledata = properties.articleDetails;
			updateState({
				articleDataObj: articledata
			});
		}
	},

	[COMPONENT_RENDERED]: componentRendered
};