import { t } from 'sn-translate';
import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import '@servicenow/now-icon';
import '@servicenow/now-text-link';
import { SnTimeAgo } from "sn-component-timeago";

const getTimeAgoString = (timeStamp) => {
	const options = {
		timeAgo: true,
		dateBoth: false,
		systemTime: true,
		dateOnly: false
  };
  
	return new SnTimeAgo({}, timeStamp, options).getTickTime().time;
}

const getSecondaryData = (sencondaryFields,artileTimeToRead)=>{
  var res = [];
  if(sencondaryFields && sencondaryFields.length>0)
    sencondaryFields.forEach((field) => {
          var x= getField(field,artileTimeToRead);
          if(x!=null)
            res.push(x);
    })
  return res;
}

const getField = (field = {},artileTimeToRead=0) => {
  const type = field.type;
	switch (type) {
	case 'string':
		return (
          <li className="sn-meta-list-item">
            <now-icon icon={field.icon} size="sm"></now-icon>
              <span>{field.text}</span>
          </li>
    );
  case 'date':
		return (
          <li className="sn-meta-list-item">
            <now-icon icon={field.icon} size="sm"></now-icon>
              <span>{t(field.text, getTimeAgoString(field.value))}</span>
          </li>
  );
  case 'timeToRead':
		return (
          <li className="sn-meta-list-item">
            <now-icon icon={field.icon} size="sm"></now-icon>
              <span>{t("{0} min read", artileTimeToRead)}</span>
          </li>
  );
	default:
		return null;
  }
};

export default ({payload, props, artileTimeToRead}) => {
  const hideKbCategoryInfo = props.hideKbCategoryInfo;
  const hideActions = props.hideActions;
  const hideNumberLink = props.hideNumberLink;
  const linkPayload = {table: 'kb_knowledge', sys_id: payload.articleResult.sysId, number: payload.articleResult.number};
  const sencondaryFields = props.secondaryFields;
  return (
    <Fragment>
      <div className="sn-card-header kb-header">
        <div className="kb-header-top">
          <div className="kb-top-left">
           {hideKbCategoryInfo? "":<div className="sn-card-row kb-breadcrumb">
              <span>{payload.articleResult.kbKnowledgeBase} {(payload.articleResult.kbCategory && payload.articleResult.kbCategory.length > 0) ? ' | ' + payload.articleResult.kbCategory : ''}</span>
            </div>}
            <div className="sn-card-row kb-title" role="heading" aria-level="1">
              <span>{payload.articleResult.shortDescription}</span>
            </div>
          </div>
          {hideActions ? "" :
            <div className="kb-top-right">
              <div className="kb-action">
                <slot name="actions"></slot>
              </div>
            </div>}
        </div>
        <div className="sn-card-row kb-header-bottom">
          <div className="sn-card-content kb-meta">
            {hideNumberLink ? "" : <div className="sn-card-row">
              <now-text-link label={payload.articleResult.number} href="javascript:void(0)" append-to-payload={linkPayload}></now-text-link>
            </div>}
            <div className="sn-card-row">
              <div className="sn-card-content">
                <ul className="sn-meta-list">
                    {getSecondaryData(sencondaryFields,artileTimeToRead)}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  )
}