import '@servicenow/now-collapse';
import '@servicenow/now-icon';
import {FIELD_COLLAPSED} from './constants';
import { t } from 'sn-translate';


const getAriaLabel= (label,collapsed)=>{
    let ariaLabel = label+" ";
	ariaLabel+= collapsed ? t('section is in the collapsed mode.') : t('section is in the expanded mode.');
	return ariaLabel;
}

export default props => {
    const {
        label,
        content,
        column,
        type,
        collapsible,
        collapsed,
        headingStyle,
        fieldStyle,
        dispatch
		} = props;
    return (
        <div>
            {collapsible?
                (<div style={{cssText: fieldStyle}}>
                        <now-collapse-trigger tabindex="0" aria-label={getAriaLabel(label,collapsed)} className = "collapse-header" controls={column} role="button" on-click={() => dispatch(FIELD_COLLAPSED, {column})}
                        on-keydown={ (event) => event.keyCode == 13 || event.keyCode == 32 ? dispatch(FIELD_COLLAPSED, {column}) : ''}>
                        <div tabindex= "-1" className="collapsible-label-container">
                            <h2 className="label" style={{cssText: headingStyle}}>
                                {label}
                                <now-icon  className={collapsed ? "label-icon selector rotate down":"label-icon selector rotate"} icon="chevron-up-fill" size="md"></now-icon>
                            </h2>
                        </div>
                    </now-collapse-trigger>
                    <now-collapse id={column} expanded={!collapsed}>
                        {type == "html" ? <p  className="content" innerHTML={content}/>:<section  className="content" innerHTML={content}/>}
                    </now-collapse>
                </div>) :
                (<div style={{cssText: fieldStyle}}>
                    <h2 className="label" style={{cssText: headingStyle}}>{label}</h2>
                    {type == "html" ? <p  className="content" innerHTML={content}/>:<section  className="content" innerHTML={content}/>}
                </div>)
            }
        </div>
    );
};